import React from 'react'
import Typed from "react-typed";

const Header = () => {
    return (
        <div className="header-wraper">
            <div className="main-info">
                <h1>Personal Portfolio</h1>
                <Typed className="typed-text" strings={["Portfolio Website", "Sodoku Solver","Trading Bot", "Fitness App"]}
                typeSpeed={40}
                backSpeed={60}
                loop
                />
                <a href="#" className="button-main"> Projects Page</a>
            </div>
        </div>
    )
}

export default Header
